var that;
export default {
	data() {
		return {
			page: 1,
			id: '',
			userinfo: '',
			list: []
		};
	},
	methods: {
		getinfo() {
			that.$request
				.request({
					url: that.api.live.blogger_goods,
					data: {
						page: that.page,
						user_id: that.id
					}
				})
				.then(res => {
					console.log(res);
					that.list = res;
				});
		},
		tpinfo(e){
			uni.navigateTo({
				url:'/pages/anchor/goods/goodsInfo/goodsInfo?id='+e.currentTarget.dataset.id
			})
		}
	},
	onLoad(option) {
		that = this;
		that.id = option.id ? option.id : '2';
		that.getinfo();
	}
};
